/* eslint-disable react/destructuring-assignment */
import React from 'react';
import { Button, Form } from 'react-bootstrap';
import { contactFromData } from '../helpers/data';
import SumbitModal from './submitModal';

export default class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.showModal = this.showModal.bind(this);
    this.hideModal = this.hideModal.bind(this);
    this.formDisabled = this.formDisabled.bind(this);
    this.state = {
      status: '',
      setModalShow: false,
      disabledForm: false,
    };
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader('Accept', 'application/json');
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.showModal();
        this.formDisabled();
        this.setState({ status: 'SUCCESS' });
      } else {
        this.setState({ status: 'ERROR' });
      }
    };
    xhr.send(data);
  }

  showModal() {
    this.setState({ setModalShow: true });
  }

  hideModal() {
    this.setState({ setModalShow: false });
  }

  formDisabled() {
    this.setState({ disabledForm: true });
  }

  render() {
    const { status } = this.state;
    return (
      <Form
        onSubmit={this.submitForm}
        action={contactFromData.formSpreeURL}
        method="POST"
      >
        <Form.Group>
          <Form.Label>{contactFromData.name.label}</Form.Label>
          <Form.Control type="text" name="name" placeholder={contactFromData.name.placeholder} required={contactFromData.name.required} disabled={this.state.disabledForm} />
        </Form.Group>
        <Form.Group>
          <Form.Label>{contactFromData.email.label}</Form.Label>
          <Form.Control type="email" name="email" placeholder={contactFromData.email.placeholder} required={contactFromData.email.required} disabled={this.state.disabledForm} />
          <Form.Text className="text-muted">
            {contactFromData.email.text}
          </Form.Text>
        </Form.Group>
        <Form.Group>
          <Form.Label>{contactFromData.phoneNumber.label}</Form.Label>
          <Form.Control type="telephone" name="telephone" placeholder={contactFromData.phoneNumber.placeholder} required={contactFromData.phoneNumber.required} disabled={this.state.disabledForm} />
        </Form.Group>
        <Form.Group>
          <Form.Label>{contactFromData.textArea.label}</Form.Label>
          <Form.Control as="textarea" name="freeText" rows="6" required={contactFromData.textArea.required} disabled={this.state.disabledForm} />
        </Form.Group>
        {status === 'SUCCESS' ? <p>{contactFromData.statusSUCCESS}</p> : <Button variant="dark" size="lg" block type="submit">{contactFromData.button}</Button>}
        {status === 'ERROR' && <p>{contactFromData.statusERROR}</p>}
        <SumbitModal show={this.state.setModalShow} onHide={this.hideModal} />
      </Form>
    );
  }
}
